<template>
  <div class="right_bar">
    <div class="right_one_container">
      <div class="top_title">
        <span class="title_label"></span>
        <div class="sort_btns"></div>
      </div>
      <div class="pie_container">
        <div class="right_one_chart"></div>
        <div class="right_chart_legend">
          <div class="legend_item" v-for="(item, i) in pieLegend" :key="i">
            <div class="label">
              <div class="pin" :style="{ background: item.color }"></div>
              <span>{{ item.name }}</span>
            </div>
            <div class="num">
              <span class="percent" :style="{ color: item.color }">{{ item.percent }}</span>
              <span class="line" :style="{ background: item.color }"></span>
              <span class="count">{{ item.count }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="right_two_container">
      <div class="top_title">
        <span class="title_label"></span>
        <div class="sort_btns">
          <div :class="{ normal_btn: true, active_btn: sortType == '1' }" @click="sortChange('sortType', '1')"></div>
          <div :class="{ normal_btn: true, active_btn: sortType == '2' }" @click="sortChange('sortType', '2')"></div>
        </div>
      </div>
      <div class="right_two_chart"></div>
    </div>
    <div class="right_three_container">
      <div class="top_title">
        <span class="title_label"></span>
        <div class="sort_btns"></div>
      </div>
      <div class="filter_bts">
        <div :class="{ filter_item: true, filter_active: filterType == '1' }" @click="sortChange('filterType', '1')">1427</div>
        <div :class="{ filter_item: true, filter_active: filterType == '2' }" @click="sortChange('filterType', '2')">759</div>
      </div>
      <div class="right_three_chart"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts"

export default {
  data() {
    return {
      sortType: "1",
      filterType: "1",
      pieLegend: [
        { name: "", percent: "20%", count: "123.54", color: "#00A2FF" },
        { name: "", percent: "20%", count: "123.54", color: "#FC2626" },
        { name: "", percent: "20%", count: "123.54", color: "#14D23E" },
        { name: "", percent: "20%", count: "123.54", color: "#ED6701" },
      ],
    }
  },
  methods: {
    sortChange(chartType, btnType) {
      this[chartType] = btnType
    },
    get3Dpie() {
      if (!this._myChart3) {
        this._myChart3 = echarts.init(document.querySelector(".right_one_chart"), null, { devicePixelRatio: 2 })
      }
      //  option
      let option = this.getPie3D(
        [
          {
            name: "",
            value: 15,
            itemStyle: {
              color: "rgba(0, 162, 255, 1)",
            },
          },
          {
            name: "",
            value: 20,
            itemStyle: {
              color: "rgba(255, 71, 128, 1)",
            },
          },
          {
            name: "",
            value: 30,
            itemStyle: {
              color: "rgba(20, 210, 62, 1)",
            },
          },
          {
            name: "",
            value: 15,
            itemStyle: {
              color: "rgba(237, 103, 1, 1)",
            },
          },
        ],
        0.7, // 
      )
      if (option) {
        this._myChart3.setOption(option)
      }
    },
    getParametricEquation(startRatio, endRatio, isSelected, isHovered, k, h) {
      // 
      let midRatio = (startRatio + endRatio) / 2

      let startRadian = startRatio * Math.PI * 2
      let endRadian = endRatio * Math.PI * 2
      let midRadian = midRatio * Math.PI * 2

      // 
      if (startRatio === 0 && endRatio === 1) {
        isSelected = false
      }

      // / k 1/3
      k = typeof k !== "undefined" ? k : 1 / 3

      //  x y  0
      let offsetX = isSelected ? Math.cos(midRadian) * 0.1 : 0
      let offsetY = isSelected ? Math.sin(midRadian) * 0.1 : 0

      //  1
      let hoverRate = isHovered ? 1.05 : 1

      // 
      return {
        u: {
          min: -Math.PI,
          max: Math.PI * 3,
          step: Math.PI / 32,
        },

        v: {
          min: 0,
          max: Math.PI * 2,
          step: Math.PI / 20,
        },

        x: function (u, v) {
          if (u < startRadian) {
            return offsetX + Math.cos(startRadian) * (1 + Math.cos(v) * k) * hoverRate
          }
          if (u > endRadian) {
            return offsetX + Math.cos(endRadian) * (1 + Math.cos(v) * k) * hoverRate
          }
          return offsetX + Math.cos(u) * (1 + Math.cos(v) * k) * hoverRate
        },

        y: function (u, v) {
          if (u < startRadian) {
            return offsetY + Math.sin(startRadian) * (1 + Math.cos(v) * k) * hoverRate
          }
          if (u > endRadian) {
            return offsetY + Math.sin(endRadian) * (1 + Math.cos(v) * k) * hoverRate
          }
          return offsetY + Math.sin(u) * (1 + Math.cos(v) * k) * hoverRate
        },

        z: function (u, v) {
          if (u < -Math.PI * 0.5) {
            return Math.sin(u)
          }
          if (u > Math.PI * 2.5) {
            return Math.sin(u) * h * 0.1
          }
          return Math.sin(v) > 0 ? 1 * h * 0.1 : -1
        },
      }
    },
    //  3D 
    getPie3D(pieData, internalDiameterRatio) {
      let series = []
      let sumValue = 0
      let startValue = 0
      let endValue = 0
      let legendData = []
      let k = typeof internalDiameterRatio !== "undefined" ? (1 - internalDiameterRatio) / (1.5 + internalDiameterRatio) : 1 / 3 // 

      //  series-surface 
      for (let i = 0; i < pieData.length; i++) {
        sumValue += pieData[i].value

        let seriesItem = {
          name: pieData[i].name,
          type: "surface",
          parametric: true,
          wireframe: {
            show: false,
          },
          pieData: pieData[i],
          pieStatus: {
            selected: false,
            hovered: false,
            k: k,
          },
        }

        if (typeof pieData[i].itemStyle != "undefined") {
          let itemStyle = {}

          typeof pieData[i].itemStyle.color != "undefined" ? (itemStyle.color = pieData[i].itemStyle.color) : null
          typeof pieData[i].itemStyle.opacity != "undefined" ? (itemStyle.opacity = pieData[i].itemStyle.opacity) : null

          seriesItem.itemStyle = itemStyle
        }
        series.push(seriesItem)
      }

      //  sumValue getParametricEquation 
      //  series-surface  series-surface.parametricEquation
      for (let i = 0; i < series.length; i++) {
        endValue = startValue + series[i].pieData.value
        series[i].pieData.startRatio = startValue / sumValue
        series[i].pieData.endRatio = endValue / sumValue
        series[i].parametricEquation = this.getParametricEquation(series[i].pieData.startRatio, series[i].pieData.endRatio, false, false, k, series[i].pieData.value)
        startValue = endValue
        legendData.push(series[i].name)
      }

      // 
      series.push({
        name: "mouseoutSeries",
        type: "surface",
        parametric: true,
        wireframe: {
          show: false,
        },
        itemStyle: {
          opacity: 0,
        },
        parametricEquation: {
          u: {
            min: 0,
            max: Math.PI * 2,
            step: Math.PI / 20,
          },
          v: {
            min: 0,
            max: Math.PI,
            step: Math.PI / 20,
          },
          x: function (u, v) {
            return Math.sin(v) * Math.sin(u) + Math.sin(u)
          },
          y: function (u, v) {
            return Math.sin(v) * Math.cos(u) + Math.cos(u)
          },
          z: function (u, v) {
            return Math.cos(v) > 0 ? 0.1 : -0.1
          },
        },
      })

      //  legendDataseries 
      let option = {
        animation: false,
        tooltip: {
          backgroundColor: "rgba(0,0,0,0.8)",
          textStyle: {
            color: "#fff",
          },
          formatter: params => {
            if (params.seriesName !== "mouseoutSeries") {
              return `${params.seriesName}<br/><span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:${
                params.color
              };"></span>${option.series[params.seriesIndex].pieData.value}`
            }
          },
        },
        xAxis3D: {
          min: -1,
          max: 1,
        },
        yAxis3D: {
          min: -1,
          max: 1,
        },
        zAxis3D: {
          min: -1,
          max: 1,
        },
        grid3D: {
          show: false,
          boxHeight: 8,
          // 
          boxDepth: 105,
          boxWidth: 105,
          viewControl: {
            //3d
            animation: false,
            alpha: 25, // 
            rotateSensitivity: [2, 0], //   0
            zoomSensitivity: 0,
            panSensitivity: 0,
            distance: 120,
            center: [0, -20, 0],
          },
          //SSAO
          postEffect: {
            //
            enable: true,
            bloom: {
              enable: true,
              bloomIntensity: 0.1,
            },
            SSAO: {
              enable: true,
            },
          },
        },
        series: series,
      }
      return option
    },
    getChart4() {
      const offsetX = 10 //bar
      const offsetY = 5 //  (bar)
      // 
      const CubeLeft = echarts.graphic.extendShape({
        shape: {
          x: 0,
          y: 0,
        },
        buildPath: function (ctx, shape) {
          const xAxisPoint = shape.xAxisPoint
          const c0 = [shape.x, shape.y] //  
          const c1 = [shape.x - offsetX, shape.y - offsetY + 5] //   
          const c2 = [xAxisPoint[0] - offsetX, xAxisPoint[1] - offsetY + 5] //  
          const c3 = [xAxisPoint[0], xAxisPoint[1] + 5] //  
          ctx.moveTo(c0[0], c0[1]).lineTo(c1[0], c1[1]).lineTo(c2[0], c2[1]).lineTo(c3[0], c3[1]).closePath()
        },
      })
      // 
      const CubeRight = echarts.graphic.extendShape({
        shape: {
          x: 0,
          y: 0,
        },
        buildPath: function (ctx, shape) {
          const xAxisPoint = shape.xAxisPoint
          const c1 = [shape.x, shape.y] //
          const c2 = [xAxisPoint[0], xAxisPoint[1] + 5] // 
          const c3 = [xAxisPoint[0] + offsetX, xAxisPoint[1] - offsetY + 5] //  
          const c4 = [shape.x + offsetX, shape.y - offsetY + 5] // 
          ctx.moveTo(c1[0], c1[1]).lineTo(c2[0], c2[1]).lineTo(c3[0], c3[1]).lineTo(c4[0], c4[1]).closePath()
        },
      })
      // 
      const CubeTop = echarts.graphic.extendShape({
        shape: {
          x: 0,
          y: 0,
        },
        buildPath: function (ctx, shape) {
          const c1 = [shape.x, shape.y + 5] //
          const c2 = [shape.x + offsetX, shape.y - offsetY + 5] //
          const c3 = [shape.x, shape.y - offsetX + 5] //
          const c4 = [shape.x - offsetX, shape.y - offsetY + 5] //
          ctx.moveTo(c1[0], c1[1]).lineTo(c2[0], c2[1]).lineTo(c3[0], c3[1]).lineTo(c4[0], c4[1]).closePath()
        },
      })
      // 
      echarts.graphic.registerShape("CubeLeft", CubeLeft)
      echarts.graphic.registerShape("CubeRight", CubeRight)
      echarts.graphic.registerShape("CubeTop", CubeTop)
      let xAxisData = ["A", "B", "C", "D", "E"]
      let seriesData = [20, 45, 80, 46, 57]
      // 
      let colorArr = [
        ["rgba(0, 114, 221, 1)", "rgba(129, 228, 255, 1)"],
        ["rgba(0, 147, 221, 1)", "rgba(0, 88, 255, 0.2)"],
        ["rgba(0, 107, 188, 1)", "rgba(1, 56, 222, 0.2)"],
      ]
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
          borderRadius: 0,
          padding: 0,
          formatter: function (params, ticket, callback) {
            const item = params[1]
            return item.name + " : " + item.value
          },
        },
        grid: {
          left: "0%",
          right: "0",
          top: "5%",
          bottom: "0%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          data: xAxisData,
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            fontSize: 12,
            interval: 0,
            color: "rgba(255, 255, 255, 0.6)",
            // 
            /* formatter: function (value) {
              const length = value.length
              if (length > 3) {
                const start = Math.floor(length / 2)
                const str = value.slice(0, start) + "\n" + value.slice(start, length)
                return str
              }
              return value
            }, */
            rotate: 45,
          },
        },
        yAxis: {
          type: "value",
          minInterval: 1,
          // y
          axisLine: {
            show: false,
          },
          // y 
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(255,255,255,0.16)",
            },
          },
          // y
          axisTick: {
            show: false,
          },
          // y
          axisLabel: {
            fontSize: 14,
            color: "#fff",
            formatter: function (value) {
              return value + "%"
            },
          },
        },
        series: [
          {
            type: "custom",
            renderItem: (params, api) => {
              const location = api.coord([api.value(0), api.value(1)])
              return {
                type: "group",
                children: [
                  // 
                  {
                    type: "CubeLeft",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0],
                      y: location[1],
                      xAxisPoint: api.coord([api.value(0), 0]),
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[1][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[1][1],
                        },
                      ]),
                    },
                  },
                  // 
                  {
                    type: "CubeRight",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0],
                      y: location[1],
                      xAxisPoint: api.coord([api.value(0), 0]),
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[2][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[2][1],
                        },
                      ]),
                    },
                  },
                  // 
                  {
                    type: "CubeTop",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0],
                      y: location[1],
                      xAxisPoint: api.coord([api.value(0), 0]),
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[0][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[0][1],
                        },
                      ]),
                    },
                  },
                ],
              }
            },
            data: seriesData,
          },
          {
            type: "bar",
            label: {
              /* normal: {
                show: true,
                position: "top",
                formatter: e => {
                  return e.value
                },
                fontSize: 16,
                color: "#43C4F1",
                offset: [0, -5],
              }, */
            },
            itemStyle: {
              color: "transparent",
            },
            tooltip: {},
            data: seriesData,
          },
        ],
      }

      if (!this._myChart4) {
        this._myChart4 = echarts.init(document.querySelector(".right_two_chart"))
      }
      this._myChart4.setOption(option)
    },
    getChart5() {
      let xaxisData = ["", "", "", "", "", "", ""]
      let yaxisData = [90, 80, 100, 70, 65, 69, 80]
      let yaxisData2 = Array(7)
        .fill(0)
        .map(() => parseInt(Math.random() * 100))
      const offsetX = 8
      const offsetY = 4
      // 
      const CubeLeft = echarts.graphic.extendShape({
        shape: {
          x: 0,
          y: 0,
        },
        buildPath: function (ctx, shape) {
          // canvasshapecustom
          const xAxisPoint = shape.xAxisPoint
          const c0 = [shape.x, shape.y]
          const c1 = [shape.x - offsetX, shape.y - offsetY]
          const c2 = [xAxisPoint[0] - offsetX, xAxisPoint[1] - offsetY]
          const c3 = [xAxisPoint[0], xAxisPoint[1]]
          ctx.moveTo(c0[0], c0[1]).lineTo(c1[0], c1[1]).lineTo(c2[0], c2[1]).lineTo(c3[0], c3[1]).closePath()
        },
      })

      // 
      const CubeRight = echarts.graphic.extendShape({
        shape: {
          x: 0,
          y: 0,
        },
        buildPath: function (ctx, shape) {
          const xAxisPoint = shape.xAxisPoint
          const c1 = [shape.x, shape.y]
          const c2 = [xAxisPoint[0], xAxisPoint[1]]
          const c3 = [xAxisPoint[0] + offsetX, xAxisPoint[1] - offsetY]
          const c4 = [shape.x + offsetX, shape.y - offsetY]
          ctx.moveTo(c1[0], c1[1]).lineTo(c2[0], c2[1]).lineTo(c3[0], c3[1]).lineTo(c4[0], c4[1]).closePath()
        },
      })

      // 
      const CubeTop = echarts.graphic.extendShape({
        shape: {
          x: 0,
          y: 0,
        },
        buildPath: function (ctx, shape) {
          const c1 = [shape.x, shape.y]
          const c2 = [shape.x + offsetX, shape.y - offsetY] //
          const c3 = [shape.x, shape.y - offsetX]
          const c4 = [shape.x - offsetX, shape.y - offsetY]
          ctx.moveTo(c1[0], c1[1]).lineTo(c2[0], c2[1]).lineTo(c3[0], c3[1]).lineTo(c4[0], c4[1]).closePath()
        },
      })

      // 
      echarts.graphic.registerShape("CubeLeft", CubeLeft)
      echarts.graphic.registerShape("CubeRight", CubeRight)
      echarts.graphic.registerShape("CubeTop", CubeTop)

      let colorArr = [
        ["rgba(0, 188, 188, 1)", "rgba(1, 169, 222, 0.2)"],
        ["rgba(0, 208, 221, 1)", "rgba(0, 194, 255, 0.2)"],
        ["rgba(0, 181, 221, 1)", "rgba(165, 255, 255, 1)"],

        ["rgba(0, 147, 221, 1)", "rgba(0, 88, 255, 0.2)"],
        ["rgba(0, 107, 188, 1)", "rgba(1, 56, 222, 0.2)"],
        ["rgba(0, 114, 221, 1)", "rgba(129, 228, 255, 1)"],
      ]

      let option = {
        backgroundColor: "black",
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
          backgroundColor: "rgba(255,255,255,0.75)",
          extraCssText: "box-shadow: 2px 2px 4px 0px rgba(0,0,0,0.3);",
          textStyle: {
            fontSize: 14,
            color: "#000",
          },
          formatter: (params, ticket, callback) => {
            const item = params[1]
            return item.name + " : " + item.value + " "
          },
        },
        grid: {
          left: "0%",
          right: "0",
          top: "5%",
          bottom: "0%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          data: xaxisData,
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLabel: {
            fontSize: 14,
            color: "#fff",
          },
        },
        yAxis: {
          type: "value",
          minInterval: 1,
          // y
          axisLine: {
            show: false,
          },
          // y 
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(255,255,255,0.16)",
            },
          },
          // y
          axisTick: {
            show: false,
          },
          // y
          axisLabel: {
            fontSize: 14,
            color: "#fff",
          },
        },
        series: [
          {
            type: "custom",
            renderItem: (params, api) => {
              const location = api.coord([api.value(0), api.value(1)])
              const xAxisPoint = api.coord([api.value(0), 0])
              const distance = 11
              return {
                type: "group",
                children: [
                  {
                    type: "CubeLeft",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0] - distance,
                      y: location[1],
                      xAxisPoint: [xAxisPoint[0] - distance, xAxisPoint[1]],
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[0][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[0][1],
                        },
                      ]),
                    },
                  },
                  {
                    type: "CubeRight",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0] - distance,
                      y: location[1],
                      xAxisPoint: [xAxisPoint[0] - distance, xAxisPoint[1]],
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[1][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[1][1],
                        },
                      ]),
                    },
                  },
                  {
                    type: "CubeTop",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0] - distance,
                      y: location[1],
                      xAxisPoint: [xAxisPoint[0] - distance, xAxisPoint[1]],
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[2][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[2][1],
                        },
                      ]),
                    },
                  },
                ],
              }
            },
            data: yaxisData,
          },
          {
            type: "custom",
            renderItem: (params, api) => {
              const location = api.coord([api.value(0), api.value(1)])
              const xAxisPoint = api.coord([api.value(0), 0])
              const distance = 11
              return {
                type: "group",
                children: [
                  {
                    type: "CubeLeft",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0] + distance,
                      y: location[1],
                      xAxisPoint: [xAxisPoint[0] + distance, xAxisPoint[1]],
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[3][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[3][1],
                        },
                      ]),
                    },
                  },
                  {
                    type: "CubeRight",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0] + distance,
                      y: location[1],
                      xAxisPoint: [xAxisPoint[0] + distance, xAxisPoint[1]],
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[4][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[4][1],
                        },
                      ]),
                    },
                  },
                  {
                    type: "CubeTop",
                    shape: {
                      api,
                      xValue: api.value(0),
                      yValue: api.value(1),
                      x: location[0] + distance,
                      y: location[1],
                      xAxisPoint: [xAxisPoint[0] + distance, xAxisPoint[1]],
                    },
                    style: {
                      fill: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                        {
                          offset: 0,
                          color: colorArr[5][0],
                        },
                        {
                          offset: 1,
                          color: colorArr[5][1],
                        },
                      ]),
                    },
                  },
                ],
              }
            },
            data: yaxisData2,
          },

          {
            type: "bar",
            itemStyle: {
              color: "transparent",
            },
            data: yaxisData,
          },
          {
            type: "bar",
            itemStyle: {
              color: "transparent",
            },
            data: yaxisData2,
          },
        ],
      }
      if (!this._myChart5) {
        this._myChart5 = echarts.init(document.querySelector(".right_three_chart"))
      }
      this._myChart5.setOption(option)
    },
  },
  created() {
    this.$nextTick(() => {
      this.get3Dpie()
      this.getChart4()
      this.getChart5()
    })
  },
  mounted() {
    let change = () => {
      if (this._myChart3) {
        this._myChart3.resize()
        this._myChart4.resize()
        this._myChart5.resize()
      }
    }
    change()
    window.addEventListener("resize", change, false) // 
  },
}
</script>

